.pxblock--debates {
    padding: 0 0 50px;
    > .container{
        max-width: 1174px;
    }
    .columns-title{
        color: $blue;
        margin-bottom: 25px;
    }
    .top-content{
        padding-bottom: 20px;
    }
    .debate-grid {
        display: block;
        margin: 0;
        & + .debate-grid{
            margin-top: 15px;
        }
        .full-link{
            display: block;
        }
        .debate-grid-inn{
            display: flex;
            flex-wrap: wrap;
            background-color: $gray;
            .preview-content {
                position: relative;
                width: 100%;
                height: 335px;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                @media screen and (min-width: 768px){
                    width: 60%;
                }
                @media screen and (min-width: 1024px){
                    width: 70%;
                }
                .debate__image {
                    position: absolute;
                    top: 0; 
                    left: 0;
                    width: 100%;
                    height: 100%;
                    &:before {
                        content: '';
                        position: absolute;
                        top: 0; 
                        left: 0;
                        width: 100%;
                        height: 100%;
                        z-index: 2;
                        background: rgba($blue, .8);                
                        transition: $speed $easing;
                    }
                    img {
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                    }
                }
                .debate__content {
                    position: relative;
                    z-index: 5;
                    color: #000000;
                    padding: 40px 25px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;            
                    h3{
                        color: $blue;
                        margin-top: 5px;
                    }
                    @media screen and (min-width: 768px){
                        padding: 50px;
                    }
                }
                .excerpt {
                    p{
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 22px;
                    }
                }
            }
            .featured-image {          
                position: relative;
                width: 100%;
                @media screen and (min-width: 768px){
                    width: 40%;
                }
                @media screen and (min-width: 1024px){
                    width: 30%;
                }
                .debate__image {
                    position: relative;
                    img {
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                    }
                }
                .debate__content {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 5;
                    color: #fff;
                    padding: 25px;
                    display: flex;
                    flex-wrap: wrap;
                    flex-direction: column;
                    justify-content: flex-end;  
                    span{
                        position: relative;
                        display: block;
                        text-transform: uppercase;
                        &::after{
                            content: '';
                            border-top: 6px solid transparent;
                            border-bottom: 6px solid transparent;
                            border-left: 11px solid #ffffff;
                            display: inline-block;
                            margin-left: 5px;
                            vertical-align: baseline;
                        }
                    }         
                }
            }
        }
    }    
     
}

.pxblock--debates + .pxblock--text-and-media{    
    > .container{
        .pxblock--text-and-media-inn{
            padding-top: 50px;
            position: relative;
            &::before{
                content: '';
                display: block;
                width: 100%;
                height: 3px;
                background-color: $blue;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

}
