.pxblock--board-of-directors {
    padding: 0 0 50px;    
    > .container{
        max-width: 1174px;
        .grid{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 1.25rem;
            @media screen and (min-width: 1024px) {
                grid-template-columns: 1fr 1fr 1fr;
                gap: 3.5rem;
            }            
        }
    }
    [data-micromodal-trigger] {
        cursor: pointer;
        img {
            opacity: 1;
            transition: $speed $easing;
           
            @include bp($m){
                height: 248px;
            }
        }
        &:hover {
            img {
                opacity: .7;
            }
        }
    }
    .profile-modal__content {
        .profile__right {
           
            .profile__content {
                overflow-y: auto;
                position: relative;
                max-height: 500px;
                padding-right: 10px;
                padding-bottom: 20px;
                .Leadership-bio {
                    overflow-y: auto;
                position: relative;
                max-height: 500px;
                padding-right: 10px;
                padding-bottom: 20px;
                }
            }
        }
    }
}
