.pxblock--form {
    padding: 0 0 50px;
    margin: 0;
    > .container{
        max-width: 1174px;
    }
    .pxblock--form-inn{
        background-color: #E7E4DC;
        padding: 50px 40px;
        .form__content{
            padding: 0 0 30px;
            p{
                font-size: 16px;
                font-weight: 400;
                line-height: normal;
                color: $black;
                a{
                    text-decoration: none;
                    color: $black;
                }
            }            
        }
        .form__body {  
            background-color: #E7E4DC;             
            .iframe{
                overflow: visible;
                height: auto;
                padding: 0;
                width: 100%;
                iframe{
                    position: static;
                    width: 100%;
                }
            }
            form{
                @media screen and (max-width: 768px){
                    flex-wrap: wrap;
                    & > div{
                        width: 100%;
                    }
                }
                input:not([type=submit]):not([type=radio]):not([type=checkbox]){
                    background-color: #f8f9f9;
                    font-family: "bookmania", serif;
                    font-size: 16px;
                    line-height: 26px;
                    color: #002D72;
                    padding: 15px 10px 9px;
                }
                input[type='submit']{
                    border: 0;
                    color: #fff;
                    text-align: center;
                    display: inline-block;
                    padding: 15px 30px 9px !important;
                    text-decoration: none;
                    transition: all 0.3s ease-in-out;
                    font-family: "bookmania", serif;
                    font-size: 16px;
                    line-height: 26px;
                    font-weight: 400;
                    cursor: pointer;
                    background-color: #002D72 !important;
                    min-width: 148px;
                    &:hover{
                        background-color: #000;
                        color: #fff;
                    }
                }
            }
        }
    }
}
    
    


