.pxblock--popouttext {
	padding: 0 0 50px;
	> .container{
        max-width: 1094px;
    }
	.popout-grid {		
		> :nth-child(1) {
			padding: 40px 25px 40px;
			position: relative;
			background: $gray;
			@media screen and (min-width: 1025px){			
				padding: 46px 40px 118px;
			}
			p{
				font-size: 16px;
				font-weight: 400;
				line-height: 22px;
				&:last-child{
					margin-bottom: 0;
				}
			}
		}

		> :nth-child(2) {
			width: 100%;
			position: relative;
			z-index: 2;
			margin: 30px auto 0;			
			padding: 40px 25px;
			background: #fff;
			border: 4px solid $gray;
			box-shadow: 4px 2px 26px 4px #0000002b;
			text-align: center;
			h5{
				font-size: 16px;
				font-weight: 600;
				line-height: normal;
				text-transform: uppercase;
				margin-bottom: 1em;
				font-family: 'bookmania',serif;
				span{
					display: block;
				}
			}
			p{
				font-size: 16px;
				font-weight: 400;
				line-height: normal;
				&:last-child{
					margin-bottom: 0;
				}
			}
			ul{
				list-style: none;
				padding: 0 !important;
				li{
					list-style: none;
					position: relative;
					padding-left: 15px;
					font-size: 16px;
					font-weight: 400;
					line-height: normal;
					&::before{
						content: '';
						display: block;
						width: 4px;
						height: 4px;
						border-radius: 100%;
						background-color: #000000;
						display: inline-block;
						vertical-align: top;
						margin: 5px 8px 0 0;
					}
				}
			}
			@media screen and (min-width: 1025px){
				margin-top: -90px;
				max-width: 620px;
				padding: 45px 40px;
			}
		}
	}
}

.pxblock--popouttext + .pxblock--form::before{
	display: none;
}