.pxblock--text-and-media {
    padding: 0 0 50px;    
    > .container{
        max-width: 1174px;
    }
    .columns-title{
        color: $blue;
    }
    .media-list-view{
        .wysiwyg {            
            p{
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
            }
        }
    }

    .column-view{
        display: grid;
        &.two-col{
            grid-template-columns: 1fr;
            grid-gap: 30px;     
            @media screen and (min-width: 1024px){ 
                grid-template-columns: 1fr 1fr;
                grid-gap: 40px;      
            }    
        } 
        &.three-col{
            grid-template-columns: 1fr;
            grid-gap: 30px;     
            @media screen and (min-width: 1024px){ 
                grid-template-columns: 1fr 1fr 1fr;
                grid-gap: 40px;      
            }  
            .column-view-items{ 
                padding: 40px;   
                justify-content: flex-start;       
                .title-area{
                    order: 2;
                    margin-top: 23px;
                    font-size: 16px;
                    font-weight: 600;
                    line-height: normal;
                }
                .text-area{
                    order: 3;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 22px;
                    margin-top: 5px;
                }
                .image-area{
                    order: 1;
                    margin-top: 0;
                    height: 56vw; 
                    width: 100%;
                    @media screen and (min-width: 1024px){ 
                        height: 22vw;
                        width: 100%;
                    }                    
                    @media screen and (min-width: 1174px){
                        height: 269px; 
                    }
                    img{
                        object-position: top;
                    }
                } 
            }
        }
        .column-view-items{
            background-color: $gray;
            padding: 30px;
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;
            justify-content: flex-end;
            .title-area{
                font-size: 16px;
                font-weight: 600;
                line-height: normal;
            }
            .text-area{
                margin-top: 20px;
                font-size: 16px;
                font-weight: 400;
                line-height: 22;
            }
            .image-area{
                width: 100%;   
                height: 45vw;             
                margin: 20px auto 0;
                @media screen and (min-width: 1024px){ 
                    height: 16vw;
                    width: 70%;
                }                    
                @media screen and (min-width: 1174px){
                    height: 307px; 
                }
                img{
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                    object-position: center;
                    border: 10px solid #ffffff;
                }
            }
            .link-area{
                margin-top: 35px;
            }
        }                   
    }
}
.pxblock--text-and-media.\!py-0 + .pxblock--text{
    margin-top: 50px;
}

