.pxblock--banner {
	position: relative;
	z-index: 1;
	min-height: 700px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-bottom: 50px;
	margin-bottom: 50px;
	height: calc(100vh - 100px);
	@media screen and (min-width: 1367px){
		&::before{
			width: 20%;
			height: calc(100% - 170px);
			position: absolute;
			content: '';
			background: linear-gradient(90deg, rgba(29,60,117,1) 0%, rgba(29,60,117,1) 40%, rgba(255,255,255,0) 100%);
			z-index: 1;
			left: 0;
			top: 170px;		
		}
		&::after{
			width: 20%;
			height: calc(100% - 170px);
			position: absolute;
			content: '';
			background: linear-gradient(270deg, rgba(29,60,117,1) 0%, rgba(29,60,117,1) 40%, rgba(255,255,255,0) 100%);
			z-index: 1;
			right: 0;
			top: 170px;		
		}
	}
	&.bg {
		&-light-blue {
			background-color: $light-blue;
		}
	}

	img{
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		object-fit: cover;
		object-position: top;
	}
	video {		
		width: 100%;
		height: 100%;
		object-position: top;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
	}
}

.sub-banner__content {
	position: relative;
	color: #fff;
	width: 100%;
	top: -40px;
	.container {
		position: relative;
		z-index: 2;
	}
	.sub-banner__layout {
		position: relative;
		max-width: 800px;
		padding: 20px;
		padding-left: 0;
		> * {
			position: relative;
			z-index: 2;
		}
		&:before {
			content: '';
			position: absolute;
			top: 0; right: 0;
			bottom: 0;
			width: 100vw;
			background: $blue;
			box-shadow: 0 0 10px 10px rgba(#000, .2);
		}
	}
}

.banner-content-block{
	position: relative;
	z-index: 2;
	width: 430px;
	background: rgba(225,222,215,0.8);
	padding:30px;
	.container{
		padding-left: 0;
		padding-right: 0;
	}
	.wysiwyg{
		color: #000000;
	}
	h3{
		font-family: "bookmania", serif;
		font-style: italic;
		font-size: 20px;
		font-weight: 400;
		line-height: normal;
		color: #1d3c75;
		margin-bottom: 0;
		& + .indented-tag{
			margin-top: 10px !important;
		}
	}
	h2{
		font-family: "bookmania", serif;
		font-size: 24px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		color: #1d3c75;
		margin-bottom: 0 !important;
		text-transform: uppercase;
		text-align: left;
		& + .indented-tag{
			margin-top: 10px !important;
		}
	}
	.indented-tag{
		font-family: "bookmania", serif;
		font-size: 16px;
		font-style: normal;
		font-weight: 400;
		line-height: 22px;
		margin-bottom: 0 !important;
		&::before{
			width: 0.875rem;
			margin-right: 0.5rem;
		}
	}
	.sub-banner__content{
		padding-top: 15px;
		top: 0;
		.sub-banner__layout{
			color: #000;
			padding: 0;
			&::before{
				display: none;
			}
			h4{
				text-transform: initial;
				font-family: "bookmania", serif;
				font-size: 16px;
				font-style: normal;
				font-weight: 600;
				line-height: normal;
				margin-bottom: 3px;
			}
			p{
				font-family: "bookmania", serif;
				font-size: 16px;
				font-style: normal;
				font-weight: 400;
				line-height: 22px;
				margin: 0;
				& + p{
					margin-top: 10px;
				}
				& + ul{
					margin-top: 10px;
				}
				& + h4{
					margin-top: 10px;
				}
			}
			ul{
				margin: 0;
				li{
					font-family: "bookmania", serif;
					font-size: 16px;
					font-style: normal;
					font-weight: 400;
					line-height: 22px;
				}
				& + h4{
					margin-top: 10px;
				}
			}			
		}
	}
}


@media screen and (max-width: 767px){
	.pxblock--banner{
		> .container{
			padding-left: 1rem;
			padding-right: 1rem;
		}

		.banner-content-block{
			width: 100%;
			padding: 20px;
			h1{
				font-size: 24px;
				line-height: 30px;
			}
		}
	}
}

.pxblock--banner + .pxblock--text {
    padding-top: 12px;
}
