.site-footer {
	background: #1d3c75;
	> .container{
		flex-wrap: wrap;
		padding-top: 2rem;
		padding-bottom: 2rem;
	}
	.footer-navigation {
		background: $blue;
		padding: 80px 0;
	}
	.footer-menu{
		width: 100%;
		justify-content: center;
		margin-bottom: 42px;
		li{
			font-family: $trajan;
			font-size: 14px;
			text-transform: uppercase;
			& + li{
				&::before{
					content: "|";
					margin-left: 20px;
					margin-right: 20px;
					display: inline-flex;					
				}
			}
		}
	}

	.logo {
		position: relative;
		z-index: 12;
		width: 150px;

		@include bp($s) {
			width: 200px;
		}

		@include bp($m) {
			width: 300px;
			z-index: inherit;
		}

		@include bp($xl) {
			width: 540px;
		}
	}

	.footer-legal {
		background: #000;
		color: #fff;
		padding: 20px 0;
		a {
			color: #fff;
		}
	}

	.legal-nav-items {
		display: flex;
		padding: 16px 0 12px !important;
		li{
			padding: 0;
			margin: 0;
			& + li{
				padding-left: 10px;				
				margin-left: 10px;
				position: relative;
				&::before{
					content: '';
					display: block;
					width: 1px;
					height: 16px;
					background-color: $white;
					position: absolute;
					top: 0;
					left: 0;
				}
			}
		}

		a {
			text-decoration: none;
		}
	}
}

@media screen and (max-width: 1023px){
	.site-footer{
		.footer-menu{
			display: grid;
			grid-template-columns: repeat(3, minmax(0, 1fr));
			gap: 1rem;
			li{
				& + li{
					&::before{
						display: none;
					}
				}
			}
		}
		.footer-bottom{
			margin-bottom: 0;
		}
	}
}

@media screen and (max-width: 767px){
	.site-footer{
		> .container{
			padding-left: 1rem;
			padding-right: 1rem;
		}		
		.logo{
			min-width: 260px !important;
		}
	}
}

@media screen and (min-width: 1024px) and (max-width: 1280px){
	.site-footer{
		.footer-menu{
			li{
				font-size: 12px;
				& + li{
					&::before{
						margin-left: 15px;
						margin-right: 15px;
					}
				}
			}
		}
	}
}

@media screen and (min-width: 1366px){
	.site-footer{
		.footer-menu{
			font-size: 16px;
		}
	}
}