	background: $blue;
	background: red;
	color: #fff;
	display: flex;
	flex-direction: column;
	height: 44px;
	justify-content: center;

	.container {
		display: flex;
		justify-content: flex-end;
		align-items: center;

		> a {
			display: block;
			height: 44px;
			line-height: 44px;
			margin-left: 15px;
		}
	}
}

