.header-container {
	padding-top: 30px;
	padding-bottom: 30px;
	// border-bottom: 1px solid #fff;

	.site-header.text-base-blue & {
		border-bottom: 1px solid $blue;

		@include bpmax($m) {
			color: $white;
			.nav-items {
				color: $white;
			}
		}

		@include bp() {
			.nav-items {
				> li {
					&.current-menu-item,
					&.current-menu-ancestor {
						&:after {
							border-bottom-color: $blue;
						}
					}
				}
			}
		}
	}
	@include bp($l) {
		padding-top: 0;
		padding-bottom: 0;
	}
}

.site-header {
	padding: 10px 0 45px;
	background: #1d3c75;
	> .container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.logo {
		position: relative;
		z-index: 12;
		width: 150px;

		@include bp($s) {
			width: 200px;
		}

		@include bp($m) {
			width: 300px;
			z-index: inherit;
		}

		@include bp($xl) {
			width: 540px;
		}
	}

	&__nav {
		width: 100%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		margin-bottom: 25px;
		.nav-items {
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			width: 100vw;
			padding: 100px 30px 60px;
			background-color: $blue;
			// color: #fff;
			z-index: 11;
			transition: 0.25s ease-in-out;
			transform: translateX(100%);

			@include bp() {
				bottom: 0;
				display: flex;
				position: static;
				width: auto;
				padding: 0;
				background-color: transparent;
				transform: none;
				> li {
					line-height: 1.5;
					font-size: 16px;
					& + li{
						margin-left: 40px !important;
						&::before{
							content: "\007C";
							display: block;
							position: absolute;
							top: 50%;
							transform: translateY(-57%);
    						left: -24px;
						}
					}
					> a {
						text-decoration: none;
						text-align: center;
						display: inline-flex;
					}
					&:after {
						content: '';
						position: absolute;
						bottom: 0;
						left: 50%;
						transform: translateX(-50%);
						border: 6px solid transparent;
						border-bottom-color: #fff;
						transition: all 0.3 ease-in-out;
						opacity: 0;
					}					
					&.current-menu-item, &.current-menu-ancestor, &:hover {
						&:after {
							opacity: 1;
						}
					}
					.sub-menu {
						padding: 15px;
						left: 50%;
						transform: translateX(-50%);
						.menu-item{
							position: relative;
							&:after {
								content: '';
								position: absolute;
								top: 15px;
								left: 8px;
								transform: translateX(-50%) rotate(90deg);
								border: 6px solid transparent;
								border-bottom-color: $blue;
								transition: all 0.3s ease-in-out;
								opacity: 0;
							}
							&.current-menu-item, &:hover {								
								&:after {
									opacity: 1;
								}
							}
						}
					}
				}

				// .sub-menu-wrap {
				// 	display: none;
				// }
			}
		}
	}

	.mobile-nav-open &__nav .nav-items {
		transform: translateX(0);
	}

	&__mobile-trigger {
		width: 40px;
		height: 20px;

		position: relative;
		margin: 0;
		transform: rotate(0deg);
		transition: 0.5s ease-in-out;
		cursor: pointer;
		border: 0;
		background-color: transparent;
		z-index: 12;
		outline: none !important;
		margin-left: auto;
		margin-top: -32px;
		@include bp() {
			display: none;
		}

		span {
			display: block;
			position: absolute;
			height: 2px;
			width: 100%;
			background-color: #fff;
			opacity: 1;
			left: 0;
			transform: rotate(0deg);
			transition: 0.25s ease-in-out;

			&:nth-child(1) {
				top: 0;
			}

			&:nth-child(2),
			&:nth-child(3) {
				top: 9px;
			}

			&:nth-child(4) {
				top: 18px;
			}
		}

		&.open span {
			&:nth-child(1) {
				top: 18px;
				width: 0;
				left: 50%;
			}

			&:nth-child(2) {
				transform: rotate(45deg);
			}

			&:nth-child(3) {
				transform: rotate(-45deg);
			}

			&:nth-child(4) {
				top: 18px;
				width: 0;
				left: 50%;
			}
		}
	}
}
.secondary-menu {
	z-index: 100;
	position: relative;
}

.header-bottom-wrap{
    width: 100%;
    justify-content: space-between;
	.logo{
		margin: 0 auto;
	}	
	.donate-social-wrap{
		position: absolute;
		right: 0;
		bottom: 50px;
		.social-nav-items{
			justify-content: center;
			padding: 7px;
		}
		.container{
			padding-left: 0;
			padding-right: 0;
			display: block;
		}
		.btn{
			margin: 0;
			border: 0;
		}
	}
}

@media screen and (max-width: 1023px){
	body .site-header .logo{
		margin: 0;
	}
	.site-header{
		padding-top: 80px;
		padding-bottom: 30px;
		.header-bottom-wrap{
			.donate-social-wrap{
				width: 100%;
				left: 50%;
				right: auto;
				top: 0;
				bottom: auto;
				transform: translateX(-50%);
				.container{
					display: flex;
					justify-content: center;
				}
				.social-nav-items{
					padding-top: 17px;
					padding-bottom: 17px;					
				}
				.btn{
					padding-top: 12px;
					padding-bottom: 12px;
				}
			}
		}

		> .container{
			padding-top: 0;
			padding-bottom: 0;
		}
	}
}

@media screen and (min-width: 1024px) and (max-width: 1280px){
	.site-header__nav{
		.nav-items{
			> li{
				font-size: 12px !important;
				& + li{
					margin-left: 30px !important;
					&::before{
						left: -19px;
					}
				}
			}
		}
	}
}
@media screen and (min-width: 1024px) and (max-width: 1199px){
	.header-bottom-wrap{
		.donate-social-wrap{
			bottom: 20px;
		}
	}
}