.pxblock--text {
    padding: 0 0 50px;
    > .container{
        max-width: 1174px;
    }
    .text{
        h2, h3{
            color: $blue;
        }
        h4{
            margin-bottom: 12px;
        }
        p{
            font-size: 16px;
            font-weight: 400;
            line-height: 22px;
            margin: 0;
            & + p{
                margin-top: 14px;
            }
            img{
                max-width: 100%;
                height: auto;
                margin-top: 60px;
            }            
            & + h1, & + h2, & + h3, & + h4, & + h5, & + h6{
                margin-top: 35px;
            }
            & + ul{
                margin-top: 14px;                
            }
        }
        ul{
            margin: 0;
            li{
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
                margin: 0;
                & + li{
                    margin: 10px 0 0;
                }
                ul{
                    margin-top: 5px;
                }
            }
            & + h1, & + h2, & + h3, & + h4, & + h5, & + h6{
                margin-top: 35px;
            }
            & + p{
                margin-top: 14px;
            }
        }
        hr{
            border: 2px solid $blue;
        }
    }
    .list-items{
        .list-item-area{
            background-color: $gray;
            padding: 50px 30px;
            & + .list-item-area{
                margin-top: 20px;
            }
        }
    }
}

