.pxblock--columns{
    padding: 0 0 50px;
    > .container{
        max-width: 1174px;
    }
    .columns-title{
        color: $blue;
        margin-bottom: 23px;
    }
    .grid-list{
        grid-gap: 7px;
        .border{
            border-style: solid;
        }
        .link-wrap{
            padding: 20px;
        }
        .group-wrap{
            .image-wrap{
                height: auto;
                @media screen and (min-width: 1024px) {
                    height: 16vw;
                }
                @media screen and (min-width: 1174px) {
                    height: 188px;
                }
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .content-wrap{
                padding-top: 20px;
                h3{
                    font-size: 20px;
                    font-weight: 400;
                    line-height: normal;
                }
                .wysiwyg{
                    p{
                        font-size: 16px;
                        font-weight: 400;
                        line-height: normal;
                        margin: 0;
                        & + p{
                            margin-top: 20px;
                        }
                    }
                }
            }
        }
    }
    .bg-base-blue\/90{
        background-color: rgb(82 111 169 / 90%);
    }
    .list{
        .group{
            padding: 0;
            & + .group{
                margin-top: 15px;
            }
        }
        .group-wrap{
            display: flex;
            flex-wrap: wrap;
            flex-direction: row-reverse;
            align-items: center;
            background: #e0ddd6;
            min-height: 223px;
            .image-wrap{
                width: 230px;
                align-self: stretch;
                img{
                    width: 100%;
                    height: 100%;
                    display: block;
                    object-fit: cover;                    
                }
            }
            .content-wrap{
                width: calc(100% - 230px);
                padding: 40px 20px 40px 40px;
                .wysiwyg{
                    p{
                        font-size: 16px;
                        font-weight: 400;
                        line-height: normal;
                        margin: 0;
                        &.h4{
                            font-size: 16px;
                            font-weight: 600;
                            line-height: normal;
                        }
                        & + p{
                            margin-top: 14px;
                        }
                    }
                }
            }
        }
    }
}

.testimonial-grid{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 0 30px;
    @media screen and (min-width: 1024px){
        grid-template-columns: 1fr 1fr;
        position: relative;
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: calc(100% - 20px);
            background-color: $blue;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }
    }
    @media screen and (max-width: 1023px){
        padding: 0 20px;
    }
    .group{
        justify-content: center;
        align-items: center;
        padding: 20px;
        p{
            font-size: 16px;
            font-weight: 400;
            line-height: 22px;
            &.text-lg{
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
                text-align: center;
            }
            &.uppercase {
                position: relative;
                padding-left: 15px;
                margin-top: 30px;
                &::before{
                    content: '';
                    display: block;
                    width: 10px;
                    height: 1px;
                    background-color: #000000;
                    position: absolute;
                    top: 10px;
                    left: 0;
                }
            }
        }
        @media screen and (max-width: 1023px){
            padding: 0;
            & + .group{
                border-top: 2px solid $blue;
                margin-top: 30px;
                padding-top: 30px;
            }
        }
    }
}

@media screen and (max-width: 1023px){
    .pxblock--columns{
        .grid-list{
            .group{
                & + .group{
                    margin-top: 7px;
                }
            }
        }
        .list{
            .group-wrap{
                display: block;
                .image-wrap{
                    width: 100%;
                }
                .content-wrap{
                    width: 100%;
                    padding: 20px;
                }
            }
        }
    }
}

