.pxblock--image-overlap {
    padding: 0 0 50px;
    .container {        
        overflow: hidden;
        max-width: 1174px;
    }
    .overlap-img-inn{
        position: relative;
        .overlap-img-area{
            width: 100%;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                margin: 0;
                border-top: 9px solid #ffffff;
            }
        }
        .primary-image {
            position: relative; 
            @media screen and (min-width: 1024px){
                height: 59.7vw;
                img{
                    border: 0;
                }
            } 
            @media screen and (min-width: 1174px){
                height: 700px;
            }      
        }

        .secondary-image {                      
            @media screen and (min-width: 1024px){
                position: absolute;
                right: 0;
                top: 31%;
                width: 50%;
                height: 45%;  
                img{
                    border-left: 9px solid #ffffff;
                }
            } 
            @media screen and (min-width: 1174px){
                
            }  
        }
        .tertiary-image{            
            @media screen and (min-width: 1024px){
                width: 50%;
                height: 50%;                
            }
            @media screen and (min-width: 1174px){
                
            }
        }
    }
}
